  display: none;
  z-index: 10;
}

#selectName:focus + #pokemonlistArea {
  position: absolute;
  display: block;
  overflow-y: scroll;
  max-height: 350px;
}

#SuggestList {
  &:hover + #pokemonlistArea {
    display: block;
  }
  li:hover {
    background-color: #f2a2b7;
    color: white;
  }
  li:after {
    font-family: "FontAwesome" !important;
    content: "\f054";
    position: absolute;
    top: 25%;
    right: 10px;
  }
}
</style>
